<script>
import Vue from "vue";
import { mapGetters, mapActions } from "vuex";
import AllTutors from "./../../tutors"
import {
  required,
  minLength,
  maxLength,
  email,
  alpha,
  numeric
} from "vuelidate/lib/validators";
const {  default: Vuelidate } = require("vuelidate");
Vue.use(Vuelidate);
const default_layout = "default";
export default {
  components: {
    AllTutors
  },
  metaInfo: {
    title: "Trusted & verified personal tutors",
    titleTemplate: "%s - | Keetela.com | Igniting minds for success!",
    meta: [
      {
        name: "description",
        content:
          "We connect you with trusted & verified one-on-one tutor near you!"
      },
      {
        name: "keywords",
        content: "Tutors, Teachers, One-on-one tutors, Primary subjects, Mathematics, English, French"
      }
    ]
  },
  name: "mentoring",
  data() {
    return {
      requestTutor: {
        names: "",
        email: "",
        tel: "",
        child: "",
        courses: []
      },
      becomeTutor: {
        names: "",
        email: "",
        tel: "",
        location: ""
      }
    };
  },
  mounted() {
    this.fetchCourses();
  },
  computed: {
    get_courses() {
      return this.$store.getters.get_courses;
    },
    request_tutor() {
      return this.$store.getters.request_tutor;
    },
    become_tutor() {
      return this.$store.getters.become_tutor;
    },
    ...mapGetters(["request_tutor", "become_tutor"]),
    layout() {
      return (this.$route.meta.layout || default_layout) + "-layout";
    }
  },
  methods: {
    requestTutorNow() {
      this.createTutorRequest(this.requestTutor);
    },
    becomeTutorNow() {
      this.createBecomeTutor(this.becomeTutor);
    },
    ...mapActions(["fetchCourses", "createTutorRequest", "createBecomeTutor"])
  },
  // validation
  validations: {
    requestTutor: {
      names: {
        required,
        alpha,
        minLength: minLength(2),
        maxLength: maxLength(45)
      },
      courses: {
        required
      },
      email: {
        required,
        email
      },
      child: {
        required,
        minLength: minLength(8),
        maxLength: maxLength(22)
      }
    },
    becomeTutor: {
      names: {
        required,
        alpha,
        minLength: minLength(2),
        maxLength: maxLength(45)
      },
      tel: {
        required,
        numeric
      },
      email: {
        required,
        email
      },
      location: {
        required,
        minLength: minLength(8),
        maxLength: maxLength(22)
      }
    }
  }
};
</script>

